import { cartAdd, getCurrentCart } from '@process-creative/slate-theme-tools';
import { Cart } from '../tools/cart/cart';
import { getElems } from '../tools/utilities/helper-functions';
import { onCartUpdated } from './bundle/bundle-common';
type ButtonObj = {
  addButton: HTMLElement;
  variantId: number;
  relatedProdElem: HTMLElement;
};

export function setupAddToCart(container: HTMLElement) {
  const buttons = getElems('[data-related-add-button]', container);

  const buttonObjs: ButtonObj[] = buttons.map((addButton) => {
    const relatedProdElem: HTMLElement = addButton.closest(
      '[data-related-product]'
    )!;

    const variantId = relatedProdElem?.getAttribute('data-variant-id');

    if (!variantId) {
      console.error('Failed to get related variant id');
    }
    return {
      addButton,
      variantId: Number(variantId),
      relatedProdElem,
    };
  });

  //set initial button state
  buttonObjs.forEach(validateAndUpdateButton);

  // add to cart
  buttonObjs.forEach((buttonObj) => {
    const { addButton, variantId } = buttonObj;
    addButton.addEventListener('click', (e) => {
      const isValid = validateVariant(buttonObj);

      if (isValid) {
        cartAdd({ items: [{ id: variantId, quantity: 1 }] }).catch((e) => {
          console.error('error :>> ', e);
          validateAndUpdateButton(buttonObj);
        });
      }
    });
  });

  onCartUpdated(() => {
    buttonObjs.forEach(validateAndUpdateButton);
  });

  function validateAndUpdateButton(buttonObj: ButtonObj) {
    const { addButton, relatedProdElem } = buttonObj;
    const isValid = validateVariant(buttonObj);
    if (isValid) {
      relatedProdElem.classList.remove('is-disabled');
      addButton.removeAttribute('disabled');
    } else {
      relatedProdElem.classList.add('is-disabled');
      addButton.setAttribute('disabled', 'disabled');
    }
  }

  function validateVariant({ relatedProdElem, variantId }: ButtonObj) {
    const variantInventory = relatedProdElem?.getAttribute(
      'data-variant-quantity'
    );
    const variantQtyInCart = (getCurrentCart() as Cart).items.reduce(
      (qty, item) => {
        if (item.variant_id == variantId) {
          qty += item.quantity;
        }
        return qty;
      },
      0
    );

    if (variantInventory === undefined || variantQtyInCart == undefined) {
      console.error('Failed to validate variant');
      return false;
    }

    const availableInventory = Number(variantInventory) - variantQtyInCart;
    if (availableInventory > 0) {
      return true;
    }
  }
}
