import { getCurrentCart, ON_CART_FINISHED, t, cartAdd, CartAdd, formatMoney, getFormat, printMoney, changeCart } from "@process-creative/slate-theme-tools";
import { Cart } from "../../tools/cart/cart";
import { ATTR_DISABLED } from "../../tools/constants/attributes";
import { CLASS_DISABLED } from "../../tools/constants/classes";
import { Product } from "../../tools/product/product";
import { Variant } from "../../tools/product/variant";
import { productFetch, variantFetch } from "../../tools/utilities/fetch-data";
import { randomNumber } from "../../tools/utilities/helper-functions";
import { ValidationState } from "../../types/types";




export type BundleParentProperties = {
  isProductBundle: string
  _bundleName: string //for marking child items with associated bundle
  _percentageDiscount: string
  _childTitles: string
}
export type BundleChildProperties = {
  isChildVariant: string
  _uniqueChildId: string
}

export type Bundle = {
  bundleProduct: Product
  childVariants: Variant[]
  percentageDiscount?: number
}



export type ChildVariant = {
  product: Product
  variant: Variant
}




function disableButton(addToCartButton: HTMLElement){
  addToCartButton.classList.add(CLASS_DISABLED);
  addToCartButton.setAttribute(ATTR_DISABLED, ATTR_DISABLED);
}
function enableButton(addToCartButton: HTMLElement){
  addToCartButton.classList.remove(CLASS_DISABLED);
  addToCartButton.removeAttribute(ATTR_DISABLED);
}




export function onAddClicked(addToCartButton: HTMLElement, action: any){
  addToCartButton.addEventListener('click', e => {
    e.preventDefault();
    action();
  })
}

export function onCartUpdated(action: any){
  $(document).on(ON_CART_FINISHED, action);
}


export async function addBundleToCart(
  data: {
    bundle: Bundle,
    bundleQuantity?: number
    addToCartButton?: HTMLElement
  }
){
  let {bundle, bundleQuantity, addToCartButton} = data;

  bundleQuantity = bundleQuantity || 1;

  const {percentageDiscount, childVariants, bundleProduct} = bundle;

  if(addToCartButton){
    disableButton(addToCartButton)  ;
    addToCartButton.innerHTML = t('products.product.cart_pending');
  }
  
  const items = setBundleProperties();
  await cartAdd({items});

  if(addToCartButton){
    const dispalyMessageDuration = 2000;
    await new Promise(resolve => setTimeout(resolve, dispalyMessageDuration));
  }


  
  function setBundleProperties(): CartAdd[]{
    const bundleProductVariantId = bundleProduct.variants[0].id;
    const bundleId = bundleProductVariantId + '-' + randomNumber({digits: 6});
    
    const childTitles = childVariants
    .reduce((variantGroups, variant) => {

      const existingVariant = variantGroups.find(g => g.variant.id == variant.id);

      if(existingVariant){
        existingVariant.quantity ++;
      }
      else{
        variantGroups.push({variant, quantity: 1});
      }
      return variantGroups;

    }, [] as {variant: Variant, quantity: number}[])
    .map(group => `${group.variant.name} ${group.quantity > 1 ? ` X ${group.quantity}` : ''}`)
    .join('|');

    
    const bundleProductItem = {
      id: Number(bundleProductVariantId),
      quantity: bundleQuantity!,
      properties: <BundleParentProperties>{
        isProductBundle: bundleId,
        _bundleName: bundleProduct.title,
        _percentageDiscount: percentageDiscount!.toString(),
        _childTitles: childTitles
      }
    }
    
    const childVariantItems = childVariants.map((variant): CartAdd => {
      return {
        id: variant.id,
        quantity: bundleQuantity!,
        properties: <BundleChildProperties>{
          isChildVariant: bundleId,
          _uniqueChildId: randomNumber({digits: 6}).toString()
        }
      }
    })
    const addArray: CartAdd[] = [bundleProductItem, ...childVariantItems];
    return addArray;
  }

}


export function updateButton(
  addToCartButton: HTMLElement, 
  validationState: ValidationState,
  bundle: Bundle,
  baseClass: string,
  quantity?: number
){
  switch(validationState){
    case 'ok':
      setValidButton(); break;
    case 'error': setDisabled(); break;
    case 'all in cart': 
    case 'not enough inventory': 
    case 'unavailable': 
      setSoldOutButton();
  }
  

  function setValidButton(){
    const {priceMoney, compareMoney} = getBundlePrice(bundle, quantity);

    const priceElem = getPriceElem(priceMoney, compareMoney);
      
    addToCartButton.innerHTML = `
      ${t('products.product.add')}
      <span class='is-divider'>|</span>
      ${priceElem}
    `;
    enableButton(addToCartButton);

    function getPriceElem(priceMoney: string, compareMoney: string){
      return `
        <p
          class="o-product-pricing ${baseClass}__add-cart-price
          ${compareMoney ? 'has-compare' : ''}"
          data-product-pricing
        >
          ${compareMoney ? `
            <span.
              class="
                o-product-pricing__money 
                o-product-pricing__compare 
                ${baseClass}__add-cart-price-money ${baseClass}__add-cart-price-compare
              "
              data-currency
              data-money="${compareMoney}"
              data-product-compare-money
            >
              ${compareMoney}
            </span.>` : ''
          }
        
          <span
            class="o-product-pricing__money o-product-pricing__price
            ${compareMoney ? 'has-compare' : ''}"
            data-currency
            data-money="${priceMoney}"
            data-product-money
          >
            ${priceMoney}
          </span>
        </p>
      `;
    }

  }

  function setSoldOutButton(){
    disableButton(addToCartButton);
    addToCartButton.innerHTML = t('products.product.sold_out');
  }


  function setDisabled(){
    disableButton(addToCartButton);
    addToCartButton.innerHTML = t('products.product.disabled');
  }

  

}

export function validateVariantAvailability(variant: Variant, quantity = 1, groupQty = 1): ValidationState{

  const cartItems = (getCurrentCart() as Cart).items;
  const {available, inventory_quantity, id, inventory_policy, inventory_management} = variant;
  
  const trackQuantityChecked = inventory_management == 'shopify';
  const continueSellingWhenOutOfStockChecked = inventory_policy == 'continue';

  if(trackQuantityChecked == false || continueSellingWhenOutOfStockChecked){
    return 'ok';
  }

  if(available == false || !inventory_quantity){
    return 'unavailable';
  }

  const variantInCartCount = cartItems.reduce((qty, item) => {
    if(item.variant_id == id){
      qty += item.quantity;
    }
    return qty;
  }, 0);

  const remainingInventory = inventory_quantity - variantInCartCount;

  if(quantity > 0 && variantInCartCount > 0 && remainingInventory <= 0){
    return 'all in cart';
  }
  if(remainingInventory - (groupQty * quantity) < 0){
    return 'not enough inventory';
  }
  return 'ok';
}



export function validateBundleAvailability(bundle: Bundle, bundleQuantity = 1): ValidationState{

  const groupedVariants = groupChildVariants();

  for(let group of groupedVariants) {
    const validationState = validateVariantAvailability(group.childVariant, bundleQuantity * group.qty);
    if(validationState != 'ok'){
      return validationState;
    }
  }
  
  return 'ok';

  /* implementation */
  
  
  function groupChildVariants(): {childVariant: Variant, qty: number}[]{

    return bundle.childVariants.reduce((groupedVariants, childVariant) => {

      const existingGroup = groupedVariants.find(g =>
        g.childVariant.id == childVariant.id
      );  
      if(existingGroup)existingGroup.qty++;
      else  groupedVariants.push({childVariant, qty: 1});
      return groupedVariants;
    }, [] as {childVariant: Variant,  qty: number}[]);

  }


}


export function getBundlePrice({childVariants, percentageDiscount, bundleProduct}: Bundle, qty = 1): {priceMoney: string, compareMoney: string}{

  const bundleFixedPrice = bundleProduct.variants[0].price * qty

  const totalVariantPrice = childVariants.reduce((totalPrice, variant) => {
    return totalPrice += variant.price * qty;
  }, 0)


  if(bundleFixedPrice > 0){
    if(bundleFixedPrice > totalVariantPrice){
      throw "Bundle price should not cost more that total of variants"
    }
  
    return {
      priceMoney: printMoney(bundleFixedPrice),
      compareMoney: totalVariantPrice > bundleFixedPrice ? printMoney(totalVariantPrice) : null
    };
  

  } else {
    const priceMoney = formatMoney(
      !Number(percentageDiscount) ? 
      totalVariantPrice : getPriceAfterDiscount() ,
        getFormat('', 'money_format')
    );
    
    const compareMoney = !Number(percentageDiscount) ? 
      null : formatMoney(totalVariantPrice, getFormat('', 'money_format'));
  
    return {
      priceMoney,
      compareMoney
    };
  

  }


  // implementation


  function getPriceAfterDiscount(): number{
    const discountAmount = totalVariantPrice * percentageDiscount!/100;
    return totalVariantPrice - discountAmount;
  }

  
}

export async function removeBundleFromCart(bundleId: string){

  const cartItems = (getCurrentCart() as Cart)?.items;
  const bundleProduct = cartItems.find(i => i.properties?.isProductBundle == bundleId);
  const childItems = cartItems.filter(i => i.properties?.isChildVariant == bundleId);
  if(!bundleProduct || !childItems) return
  changeCart([
    {key: bundleProduct.key.toString(), quantity: 0},
    ...childItems.map(item => ({key: item.key.toString(), quantity: 0}))
  ]);
}


export async function changeBundleQuantity(bundleId: string, quantity: number): Promise<ValidationState>{
  const cartItems = (getCurrentCart() as Cart).items;
  
  const bundleProductItem = cartItems.find(item => item.properties?.isProductBundle == bundleId)!;
  const bundleChildItems = cartItems.filter(item => item.properties?.isChildVariant == bundleId);

  const bundleProduct = await productFetch(bundleProductItem.handle);
  const childVariantObjs = bundleProduct.metafields.childVariants;
  const childVariants = await fetchBundleVariantsFromShopify();

  const bundle: Bundle = {
    bundleProduct,
    childVariants,
    percentageDiscount: Number(bundleProduct.metafields.percentageDiscount)
  }


  const quantityAdded = quantity - bundleProductItem.quantity;
  const validationState = validateBundleAvailability(bundle, quantityAdded);

  if(validationState == 'ok'){
    await updateCartQuantity();
  }

  return validationState;

  //implementation


  async function fetchBundleVariantsFromShopify(): Promise<Variant[]>{
    const variants: Variant[] = await Promise.all(
      childVariantObjs!.map(varObj => {
        const varObjSplit = varObj.split(':')
        const productHandle = varObjSplit[0]
        const variantId = Number(varObjSplit[1])
        return variantFetch(productHandle, variantId)
      })
    );

    return variants;

  }




  function updateCartQuantity(){
    return changeCart([
      {key: bundleProductItem.key.toString(), quantity},
      ...bundleChildItems.map(item => ({key: item.key.toString(), quantity}))
    ]);
  }

}
